export const isBrowser = typeof window !== "undefined";

export const checkInputNumericOnly = (e) => {
  // –†–∞–∑—Ä–µ—à–∞–µ–º: backspace, delete, tab –∏ escape
  if (e.keyCode === 46 || e.keyCode === 8 || e.keyCode === 9 || e.keyCode === 27 ||

    // –†–∞–∑—Ä–µ—à–∞–µ–º: Ctrl+A
    ((e.ctrlKey || e.metaKey) && e.keyCode === 65) ||

    // –†–∞–∑—Ä–µ—à–∞–µ–º: Ctrl+C / Cmd+C
    ((e.ctrlKey || e.metaKey) && e.keyCode === 67) ||

    // –†–∞–∑—Ä–µ—à–∞–µ–º: Ctrl+V / Cmd+V
    ((e.ctrlKey || e.metaKey) && e.keyCode === 86) ||

    // –†–∞–∑—Ä–µ—à–∞–µ–º: Ctrl+X / Cmd+X
    ((e.ctrlKey || e.metaKey) && e.keyCode === 88) ||

    // –†–∞–∑—Ä–µ—à–∞–µ–º: home, end, –≤–ª–µ–≤–æ, –≤–ø—Ä–∞–≤–æ
    (e.keyCode >= 35 && e.keyCode <= 39))
  {
    return
  } else {
    // –ó–∞–ø—Ä–µ—â–∞–µ–º –≤—Å–µ, –∫—Ä–æ–º–µ —Ü–∏—Ñ—Ä –Ω–∞ –æ—Å–Ω–æ–≤–Ω–æ–π –∫–ª–∞–≤–∏–∞—Ç—É—Ä–µ, –∞ —Ç–∞–∫ –∂–µ Num-–∫–ª–∞–≤–∏–∞—Ç—É—Ä–µ
    if ((e.keyCode < 48 || e.keyCode > 57) && (e.keyCode < 96 || e.keyCode > 105 )) {
      e.preventDefault()
    }
  }
}