import React from "react";
import Helmet from "react-helmet";
import AdditionTable from "../Table/Table";
import Navigation from "../Navigation/Navigation";
import './addition.scss';

const Step2 = (props) => {
  const title = "–®–∞–≥ 2: –ü–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö";

  const handleSubmitBtnClick = (e) => {
    e.preventDefault();
    props.onSubmit();
  }

  const handleGoBackBtnClick = (e) => {
    e.preventDefault();
    props.onGoBackBtnClick();
  }

  return (
    <>
      <Helmet>
        <title>{title}</title>
      </Helmet>
      <h3 className="addition__title">{title}</h3>
      <AdditionTable />
      <Navigation 
        hasPrevBtn 
        hasNextBtn 
        handleGoBackBtnClick={handleGoBackBtnClick} 
        handleSubmitBtnClick={handleSubmitBtnClick} 
        nextBtnDescription="–ü–µ—Ä–µ–π—Ç–∏ –∫ —Ä–∞—Å—Å—á–µ—Ç—É –¥–∞–Ω–Ω—ã—Ö" 
      />
    </>
  )
}

export default Step2;